// components/HeroCarousel/HeroCarousel.tsx
'use client';
import React, { useState, useEffect, useRef } from 'react';
import styles from './HeroCarousel.module.css';
import ScrollSection from '../ScrollSection/ScrollSection';

const HeroCarousel = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [progress, setProgress] = useState(0);
  const [isPlaying, setIsPlaying] = useState(true);
  const progressInterval = useRef<NodeJS.Timeout | null>(null);
  const videoRefs = useRef<(HTMLVideoElement | null)[]>([]);
  const fgVideoRefs = useRef<(HTMLVideoElement | null)[]>([]);

  const carouselItems = [
    {
      bgVideo: '/background.mp4',
      fgVideo: '/hero_video.mp4',
      title: 'A precious new hybrid',
      nextTitle: 'Venice film festival',
    },
    {
      bgVideo: '/hero_video.mp4',
      fgVideo: '/video.mp4',
      title: '3 is the magic number',
      nextTitle: 'Creative Alchemy',
    },
    {
      bgVideo: '/video.mp4',
      fgVideo: '/hero_video2.mp4',
      title: 'Shake up the senses',
      nextTitle: 'Time is an illusion',
    },
    {
      bgVideo: '/hero_video2.mp4',
      fgVideo: '/background.mp4',
      title: 'Appear and disappear',
      nextTitle: 'The power of transformation',
    },
  ];

  const startProgress = () => {
    if (progressInterval.current) clearInterval(progressInterval.current);
    setProgress(0);

    progressInterval.current = setInterval(() => {
      setProgress((prev) => {
        if (prev >= 100) {
          handleNext();
          return 0;
        }
        return prev + (100 / (8 * 10)); // 8 second duration
      });
    }, 100);
  };

  const handleNext = () => {
    setActiveIndex((prev) => (prev + 1) % carouselItems.length);
    setProgress(0);
  };

  const handlePrev = () => {
    setActiveIndex((prev) => (prev - 1 + carouselItems.length) % carouselItems.length);
    setProgress(0);
  };

  useEffect(() => {
    if (isPlaying) {
      startProgress();
    } else if (progressInterval.current) {
      clearInterval(progressInterval.current);
    }

    return () => {
      if (progressInterval.current) {
        clearInterval(progressInterval.current);
      }
    };
  }, [isPlaying, activeIndex]);

  useEffect(() => {
    const safePlay = (video: HTMLVideoElement | null) => {
      if (!video) return;
      try {
        video.currentTime = 0;
        video.play().catch(err => console.warn('Autoplay failed:', err));
      } catch (err) {
        console.warn('Autoplay failed:', err);
      }
    };

    // Reset all videos
    videoRefs.current.forEach((v) => v && (v.currentTime = 0));
    fgVideoRefs.current.forEach((v) => v && (v.currentTime = 0));

    // Play active videos
    safePlay(videoRefs.current[activeIndex]);
    safePlay(fgVideoRefs.current[activeIndex]);
  }, [activeIndex]);

  return (
    <div className={styles.heroWrapper}>
      <section className={styles.heroCarousel}>
        {/* Background Carousel - Full View */}
        <div className={styles.bgCarousel}>
          <div className={styles.videos}>
            {carouselItems.map((item, index) => (
              <div
                key={`bg-${index}`}
                className={`${styles.videoContainer} ${index === activeIndex ? styles.active : ''}`}
              >
                <video
                  ref={(el) => {videoRefs.current[index] = el}}
                  className={styles.video}
                  src={item.bgVideo}
                  playsInline
                  muted
                  loop
                />
              </div>
            ))}
          </div>
        </div>

        {/* Small Foreground Carousel - Left Side */}
        <div className={styles.fgCarousel}>
          <div className={styles.fgVideos}>
            {carouselItems.map((item, index) => (
              <div
                key={`fg-${index}`}
                className={`${styles.fgVideoContainer} ${index === activeIndex ? styles.active : ''}`}
              >
                <video
                  ref={(el) => {fgVideoRefs.current[index] = el}}
                  className={styles.fgVideo}
                  src={item.fgVideo}
                  playsInline
                  muted
                  loop
                />
              </div>
            ))}
          </div>
        </div>

        {/* Text Content */}
        <div className={styles.textCarousel}>
          <div className={styles.textCarouselItem}>
            <div>
              <h1 className={styles.siteHeadline}>
                {carouselItems[activeIndex].title.split(' ').map((word, i) => (
                  <React.Fragment key={i}>
                    {i > 0 && <span className={styles.space}> </span>}
                    {word.split('').map((char, j) => (
                      <span
                        key={j}
                        className={styles.char}
                        style={{
                          opacity: 1,
                          filter: 'blur(0px) brightness(1)',
                          willChange: 'transform',
                        }}
                      >
                        {char}
                      </span>
                    ))}
                  </React.Fragment>
                ))}
              </h1>
            </div>
            <div className={styles.cta}>
              <p className={styles.caption}>Read article</p>
            </div>
          </div>
        </div>

        {/* Navigation and Next Up Section - Right Side */}
        <div className={styles.rightSideControls}>
          <nav className={styles.nav}>
            <div className={styles.navContainer}>
              <button 
                className={styles.prev} 
                onClick={handlePrev}
                aria-label="Previous slide"
              >
                <svg viewBox="0 0 4 5" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M0 2.5L3.75 4.66506L3.75 0.334936L0 2.5Z" fill="currentColor" />
                </svg>
                <span className={styles.navHoverEffect}></span>
              </button>
              <div className={styles.navDivider}></div>
              <button 
                className={styles.next} 
                onClick={handleNext}
                aria-label="Next slide"
              >
                <svg viewBox="0 0 4 5" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M0 2.5L3.75 4.66506L3.75 0.334936L0 2.5Z" fill="currentColor" />
                </svg>
                <span className={styles.navHoverEffect}></span>
              </button>
            </div>
          </nav>
          
          <div className={styles.nextUp}>
            <p className={styles.caption}>Next up</p>
            <p className={styles.nextTitle}>
              {carouselItems[(activeIndex + 1) % carouselItems.length].nextTitle}
            </p>
            <div
              className={styles.progressBar}
              style={{ '--p': progress } as React.CSSProperties}
            />
          </div>
        </div>
      </section>

      {/* Scroll Section - Initially showing 40% */}
      
    </div>
  );
};

export default HeroCarousel;